import React, { Component } from 'react';
import { connect } from 'react-redux';
import Dashboard from './Dashboard';
import withRouter from '../../../routes/WithRouter';
import { onBoardingSteps } from '../../../../utils';
import { TAGSHOP_USER_HOME_DATA } from '../../../../actions/sceneActions';

class Home extends Component {

       constructor(props) {
       super(props);
              this.state = {
                     isVisible:false
              }
     
       }

    componentDidMount(){
       // this.props.TAGSHOP_USER_HOME_DATA()

       setTimeout(() => {
           this.setState({ isVisible : true})
       }, 8000);
 
    }

    dashBoardSteps = () => {
       const { stapData } = this.props;
       if (!stapData) {
           return false;
       }
       const requiredSteps = ['FEED_CREATED', 'PRODUCT_ADDED', 'APPS_MORE_USED', 'WEBSITE_VISITED', 'FEED_CUSTOMIZED', 'CUSTOMIZATION_SAVED', 'EMAIL_VISITED', 'EMAIL_PUBLISHED', 'EMAIL_PLATFORM_SELECTED', 'SHOPON_VISITED', 'SHOPON_BIO_ADDED', 'SHOPON_LINK_COPIED'];
       return requiredSteps.every(step => stapData[step] === true);
   };

       render() {
              const { stapData, userData, navigate } = this.props;
              let login_number = userData?.user?.login_number
              return <div className="full-content home_ position-relative">
                     { this.state.isVisible ? userData?.collaborator && Object.keys(userData.collaborator).length > 0 ? <Dashboard/> : userData ? onBoardingSteps() ? this.dashBoardSteps() ? <Dashboard /> : login_number > 2 ? <Dashboard /> : navigate('/steps') : navigate('/onboarding') : <></> : null }
              </div>
       }
}

const mapStateToProps = state => {
       return {
              stapData: state.dashboardData?.dashboarddata?.extra_data,
              userData: state.authorized?.userData?.user_data,
       }
}

const mapDispatchToProps = dispatch => {
       return {
              TAGSHOP_USER_HOME_DATA: () => dispatch(TAGSHOP_USER_HOME_DATA()),
       }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Home))