import React, { useState, useEffect } from 'react';
import Modal from 'react-bootstrap/Modal';
import { useSelector } from 'react-redux';
import { HUBSPOT_URL } from '../../actions/api';
import { Button, Image } from 'react-bootstrap';
import { baseUrl } from '../../Assets/AssetsHelper';
import { useNavigate } from 'react-router-dom';
function PlanLimitExceed() {
    const [show, setShow] = useState(false);
    const navigate = useNavigate()
    const { gallery_views_limit } = useSelector(state => state.authorized)

    useEffect(() => {
        const timer = setTimeout(() => {
            setShow(true);
        }, 100);
        return () => clearTimeout(timer);
    }, []);

    const handleClose = () => {
        setShow(false)
    }
    return (
        <Modal show={show} backdrop="static" keyboard={false} size={'lg'} aria-labelledby="planLimit" centered>
            <Modal.Body>
                {JSON.parse(localStorage.getItem('isAdmin')) ?
                <Button variant='close' className="position-absolute end-0 top-0 m-5" onClick={handleClose}></Button>
                : <></>}
                <div className="p-lg-10 p-sm-8 mt-5 text-center">
                    <Image className='mb-6' src={baseUrl('media/images/error/warning.svg')} width="65" height="65" fluid/>
                    <h2 className="h3">Your gallery is about to reach {gallery_views_limit} views</h2>
                    <p className="mb-6 fs-8 text-muted fw-medium">Your account is close to reaching the maximum limit of gallery views as per your plan. Upgrade to our higher plans to keep flaunting engaging content on your website</p>
                    <div className="text-center">
                        <button type="button" className="btn btn-primary me-4" onClick={() => { navigate('/price'); document.body.classList.add('dashUpgradeModal_opened') }}>Upgrade Now</button>
                        <a type="button" href={HUBSPOT_URL} target='_blank' rel='noreferrer' className="btn btn-outline-dark me-4 btn-min-w">Book a call</a>
                    </div>
                </div>
            </Modal.Body>
        </Modal>
    );
}
export default PlanLimitExceed;